.footer {
  background-color: var(--color-brand-normal);
  padding: 32px 0px;
}

.footer-container {
  display: flex;
  gap: 20px;
}

.footer-nav,
.footer-address {
  display: flex;
  flex-basis: 50%;
  gap: 20px;
}

.footer-link {
  text-decoration: none;
  color: var(--color-primary-light);
  font-weight: 400;
  font-size: 1rem;
}

.footer-link:hover {
  color: var(--color-brand-light);
}

.footer-logo,
.footer-menu {
  flex-grow: 1;
}

.address-list,
.address-info {
  flex-grow: 1;
}

.address-link {
  color: var(--color-primary-light);
  font-size: 16px;
  font-weight: 300;
  text-decoration: none;
}

.address-link:hover {
  color: var(--color-brand-normal);
}

.address-copyright {
  font-size: 14px;
}

.address-icon {
  fill: var(--color-primary-light);
}

.address-icon:hover {
  fill: var(--color-brand-normal);
}
